<template>
  <div class="chat-container" >
    <!--  -->
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.back()"
      fixed
    />
    <!-- / -->

    <!--  -->
    <div class="message-list" ref="message-list">
      <div
        class="message-item"
        :class="{ reverse: item.isMe }"
        v-for="(item,index) in messages"
        :key="index"
      >
        <van-image
          class="avatar"
          slot="icon"
          round
          width="30"
          height="30"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
        <div class="title">
          <span>{{ item.msg }}</span>
        </div>
      </div>
    </div>
    <!-- / -->

    <!--  -->
    <van-cell-group class="send-message">
      <van-field v-model.trim="message" center clearable>
        <van-button
            slot="button"
            size="small"
            type="primary"
            @click="onSend"
            ></van-button>
      </van-field>
    </van-cell-group>
    <!-- / -->
  </div>
</template>

<script>
import io from 'socket.io-client'
import { getItem, setItem } from '@/utils/storage.js'
export default {
  name: 'UserChat',
  data () {
    return {
      message: '',
      socket: null, // createdsocket
      messages: getItem('chat-messages') || [] // 
    }
  },
  watch: {
    // 
    messages (value) {
      setItem('chat-messages', value)
      //  vueAPI $nextTick
      // dom
      this.$nextTick(() => {
        this.onScrollBottom()
      })
    }
  },
  created () {
    // websocket
    const socket = io('http://ttapi.research.itcast.cn')
    this.socket = socket

    socket.on('connect', function () {
      console.log('')
    })

    // 
    window.socket = socket
    // 
    socket.on('message', message => {
    //   debugger
      console.log(message)
      this.messages.push(message)
    })
  },
  methods: {
    onSend () {
      const message = this.message
      if (!message.length) {
        return
      }
      // 
      const data = {
        msg: message,
        timestamp: Date.now(),
        isMe: true // 
      }
      this.socket.emit('message', data)
      // 
      this.messages.push(data)
      // 
      this.message = ''
    },
    onScrollBottom () {
      this.$refs['message-list'].scrollTop = this.$refs['message-list'].scrollHeight
    }
  }
}
</script>

<style scoped lang="less">
  .chat-container {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 46px 0 50px 0;
    top: 0;
    left: 0;
    box-sizing: border-box;
    background: #f5f5f6;
    .message-list {
      height: 100%;
      overflow-y: scroll;
      .message-item {
        display: flex;
        align-items: center;
        padding: 10px;
        .title {
          background: #fff;
          padding: 5px;
          border-radius: 6px;
        }
        .avatar {
          margin-right: 5px;
        }
      }
      .reverse {
        flex-direction: row-reverse;
        .title {
          margin-right: 5px;
        }
      }
    }

    .send-message {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      background: #f5f5f6 !important;
    }
  }
</style>
